import React, { Component } from 'react';
import $ from 'jquery';

class Bio extends Component {
  constructor(props) {
    super(props);

    this.handleLoad = this.handleLoad.bind(this);
  }

  handleLoad() {
    $('.bio-image').fadeIn();
    $('.bio-page').fadeIn();
  }

  render() {
    return (
      <div hidden className="bio-page">
        <div className="bio-image-container">
          <img hidden 
            className="bio-image" 
            src={"https://s3.amazonaws.com/images.phanaq.com/annie_cropped.jpeg"} 
            alt="annie_cropped.jpeg"
            onLoad={this.handleLoad}/>
        </div>
        <div className="bio-text">
          <h3>Bio</h3>
          <div>
            Annie Phan is an aspiring data scientist currently pursuing a master's degree in Computational Analysis and Public Policy at the University of Chicago. She is interested in examining the intersection of data and policy decisions in a wide variety of areas, including race, crime, health, and international development.<br/><br/>
            Previously, she has worked as a software engineer for start-ups and mid-sized tech companies based out of Boston and San Francisco.<br/><br/><br/>
            Annie grew up in Round Rock, Texas and studied at MIT, where she earned a bachelor's degree in Electrical Engineering and Computer Science in 2017. Some of her hobbies include photography, ultimate frisbee, tennis, sewing, yarn crafts, cycling, and hiking. She does not turn down chances to try something new.
          </div>
        </div>
        <div className="cv">
          <h3>CV</h3>
        </div>
      </div>
    )
  }
}

export default Bio;



