import React, { Component } from 'react';
import $ from 'jquery';

class Projects extends Component {
  componentDidMount() {
    setTimeout(function() { 
      $('.project-page').fadeIn();
    }, 100);
  }

  render() {
    return (
      <div hidden className="project-page">
        <div>
            in progress
        </div>
      </div>
    )
  }
}

export default Projects;



